import { Link } from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import "./components.css"
import { useSelector } from "react-redux";

function AppNavbar(){
  const cart = useSelector(state=>state.cart)
  let num = cart.length
    return(
        <>
          <Navbar expand="md" fixed="top" className="shadow-sm bg-[#23272F]">
          <Container>
            <Link to={"/Friday_React_Store"} className="navbar-brand">
               <div>
                  <button data-text="Awesome" className="button text-gray-400 ms-2">
                    <span className="actual-text">&nbsp;uiverse&nbsp;</span>
                    <span className="hover-text" aria-hidden="true">&nbsp;uiverse&nbsp;</span>
                  </button>
              </div>
            </Link>
            <Link to={"cart"} className="Btn self-center mt-2 text-white flex items-center">
                {
                  num==0?<svg className="svgIcon w-5 h-5 text-gray-800 dark:text-white" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="currentColor" viewBox="0 0 20 16">
                  <path d="M19 0H1a1 1 0 0 0-1 1v2a1 1 0 0 0 1 1h18a1 1 0 0 0 1-1V1a1 1 0 0 0-1-1ZM2 6v8a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V6H2Zm11 3a1 1 0 0 1-1 1H8a1 1 0 0 1-1-1V8a1 1 0 0 1 2 0h2a1 1 0 0 1 2 0v1Z"/>
                  </svg>:<p className="mb-1 cart-length">{num}</p> 
                }
                   
                <span className="text">Checkout</span>
            </Link>
          </Container>
          </Navbar>
        </>
    )
}

export default AppNavbar;